@import '_var.scss';

/* оформление содержимого над самой нартикой ниже top line */
.block {
	padding: 60px 0;
}
.block_title {
	text-align: center;
}
.block_title h2 {
	font-family: "OswaldBold", sans-serif;
 @include font-size(30px);
 color: $aksent_color;
 text-transform: uppercase;
 margin-bottom: 18px;

}
.block_title h2 span {
color: $red-color;
}
.block_descr {
color: $bacis_color_font;
text-align: center;
 @include font-size(16px);
 margin-bottom: 45px;
}


/*  3 тумб преимуществ в ряд */
ul.tambl_row-three {
  margin: 0;
  margin-bottom: -1%;
  margin-left: -1%;
  padding: 0;
  list-style-type: none;
   }

.tambl_item-three {
  float: left;
  width: 32.33333%;
  margin-bottom: 1%;
  margin-left: 1%;
  padding: 10px 5px;
  text-align: center; }
.tambl_item-h {
margin-bottom: 60px;
}


.tambl-title {
  text-transform: uppercase;
  color: $aksent_color;
  	font-family: "OswaldBold", sans-serif;;
 @include font-size(18px);
  margin: 10px 0 30px 0;
}

.title-text p {
 @include font-size(13px);
color: $bacis_color_font;
margin-bottom: 30px;
  }
.title-text p:last-child {
margin-bottom: 0;
}


.icon {
	background: url(../images/sprite.png) no-repeat;
	display: inline-block;
}
.icon-barket  {
background-position: 0px -162px;
width: 74px ;
height: 93px;
}
.icon-document  {
background-position: 0px -375px;
width: 74px ;
height: 93px;
}
.icon-finger_up  {
background-position: 0px -588px;
width: 74px ;
height: 93px;
}
.icon-flug  {
background-position: 0px -801px;
width: 74px ;
height: 93px;
}
.icon-heart  {
background-position: 0px -1014px;
width: 74px ;
height: 93px;
}
.icon-magnify  {
background-position: 0px 0px;
width: 42px ;
height: 42px;
}
.icon-medicin  {
background-position: 0px -1227px;
width: 74px ;
height: 93px;
}


/* // блок с тренерами */

.trainer_fon {
 background: #000 url('../images/fon_trainers.jpg') center center no-repeat;
min-height:581px;
background-size: cover;
}
.trainer_fon .block_title h2,
.galary .block_title h2,
.price_fon .block_title h2
 {
color: #fff;
position: relative;
text-align: center;
margin-bottom: 65px;
 }
 .galary .block_title h2{
  color: $aksent_color;
  margin-bottom: 35px;
 }
.trainer_fon .block_title h2:before,
.price_fon .block_title h2:before,
.galary .block_title h2:before {
position: absolute;
content: "";
width: 45px;
height: 1px;
background-color: #fff;
left:50%;
bottom: -8px;
margin-left: -22px;
text-align: center;
}
.galary .block_title h2:before {
  background-color: $aksent_color;
  }

.galary .block_descr
{
  margin-bottom: 70px;

}



/*  4 тумб преимуществ в ряд */
ul.tambl_row-four {
  margin: 0;
  margin-bottom: -2%;
  margin-left: -2%;
  padding: 0;
  list-style-type: none;
   }

.tambl_item-four {
  float: left;
  width: 23%;
  margin-bottom: 2%;
  margin-left: 2%;
  padding: 0;
  text-align: center;
 background-color: #fff;
  }
.tambl_item-four:hover {
 background-color: #fff;

}




.trainer_item {
  padding: 0;
  margin: 0;
}
.trainer_item img {
  width: 100%;
  height: auto;
}
.trainer_item_text {
height: 70px;
margin: 0;
background-color: #fff;
text-transform: uppercase;
 display: flex;
 align-items: center;
 justify-content: flex-start;
}
.trainer_item_box {
padding-left: 18px;
}
.trainer_item_name {
  @include font-size(14px);
  line-height: (24px/14px);
  font-family: "OswaldBold", sans-serif ;
  color: $color_form;
  margin-bottom: 9px;
}
.trainer_item_prof {
color: #afafaf;
text-align: left;
  @include font-size(12px);
  font-family: "OswaldRegular", sans-serif;
}
.galary .tambl_item-four .trainer_item_box {
padding-left: 0;
}
.galary .tambl_item-four .trainer_item_box .trainer_item_name {
text-align: left;
}



/* оформление цен */

.price_fon {
background: #e25050;

}

.price_fon_item
{
background: #d14343;
color: #fff;
border: 1px solid transparent;
padding: 45px 60px 50px 60px;
@include trans;

}
.price_fon_item:hover {
border: 1px solid #fff;
background-color: transparent;
}
.price_title, .price_number {
  font-family: "OswaldBold", sans-serif;
   @include font-size(18px);
   text-transform: uppercase;
   margin-bottom: 50px;

}
.price_number {
     @include font-size(36px);
}
.price_block {
@include font-size(14px);

}
.price_block ul li {
 margin-bottom: 24px;
}
.price_block ul li .fa {
  margin-right: 11px;
  font-weight: 200;
  }


.price_buton {
display: inline-block;
padding: 14px 24px;
background: #e25050;
color: #fff;
text-align: center;
text-transform: uppercase;
@include font-size(18px);
font-family: "OswaldBold", sans-serif;
@include trans;
cursor: pointer;
}
.price_fon_item:hover .price_buton,
.price_buton:hover {
background: #fff;
color: $red-color;
}


.social_block {
background-color: $color_form;
}
.social_box {
  padding: 30px 0;
 border-bottom: 1px solid #323232;
display: flex;
align-items: center;
justify-content: space-between;
}


.social_links {
  padding: 0;
}
.social_links ul li {
display: inline-block;
  }

.social_links ul li a {
    @include font-size(13px);
    padding: 7px;
    color: #fff;
     @include trans;
  }
  .social_links ul li a:hover {
    color: $red-color;

  }
.twitter_block {
align-items: flex-start;
display: flex;
justify-content: flex-start;
}


.twitter_block_icon {
-ms-align-self: center;
align-self: center;
  width: 20px;
  height: 20px;
  color: #fff;
}
.twitter_block_text {
width: 570px;
padding-left: 10px;
    color: #fff;
      @include font-size(13px);
    font-family: "OpenSansLight", sans-serif;
}

.twitter_block_text span
{
  color:#717171;
}

.footer-fon {
 background: $color_form url('../images/foter_fon_map.png') center center no-repeat;
min-height:420px;
background-size: cover;
position: relative;

}
.footer-fon:before {
position: absolute;
content: "";
background: rgba($color_form, 0.2);
width: 100%;
height: 100%;
 }





.footer_box {
padding: 0;
margin: 60px 0 100px 0;

}

.footer_left {
float: left;
width: 30%;
}

.footer_right {
float: left;
width: 70%;


}
.footer_title {
  font-family: "OswaldBold", sans-serif;
   @include font-size(14px);
   color: #fff;
text-transform: uppercase;
margin-bottom: 25px;
}
.footer_text {
font-family: "OpenSansLight", sans-serif;
 @include font-size(12px);
 line-height: (21px/12px);
 color: #c6c6c6;
}