@import '_var.scss';
/* font-family: "OswaldBold"; */
@font-face {
	font-family: "OswaldBold";
	src: url("../fonts/OswaldBold/Oswald-Bold.eot?#iefix")format("embedded-opentype"),
	url("../fonts/OswaldBold/Oswald-Bold.woff") format("woff"),
	url("../fonts/OswaldBold/Oswald-Bold.ttf") format("truetype");
	font-style: normal;
	font-weight: normal;
}
/* font-family: "OswaldRegular"; */
